/* eslint-disable no-unused-vars */
import { assignAdminNavbar, calculateAgeFromDOB, fetchUrl, getToken, logoutBtnOnClick, redirectTo } from "../util";
import { ROLES } from "../util/constants";
import { initSearch } from "../util/search";
import {
  XANO_ADMIN_SIGN_IN_REDIRECT,
  XANO_SUPER_ADMIN_DASHBOARD_URL,
  XANO_SUPER_ADMIN_USER_SEARCH_URL,
} from "../util/xano_urls";

window.onload = async function () {
  logoutBtnOnClick("/admin/sign-in", "sidebar-logout-btn");

 const token = getToken(); // ? get token (saved from signin page)

  if (token) {
    let result = await fetchUrl(XANO_SUPER_ADMIN_DASHBOARD_URL, "GET", false, token);

    if (result.isFetchOk !== undefined && !result.isFetchOk) {
      console.error(result.message);
      if (
        result.payload &&
        result.payload.role === ROLES.ADMIN &&
        result.payload.gyms &&
        result.payload.gyms.length > 0
      )
        redirectTo(`/admin/gym/g?gymCode=${result.payload.gyms[0].gym_id}`);
      else redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
      return;
    }

    assignAdminNavbar(ROLES.SUPER_ADMIN);
    getSuperAdminData(result, token);
  } else redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
};

async function getSuperAdminData(data, token) {
  const { users, gyms, finance } = data;

  appendProfitChartData(finance);
  appendUsersChartData(users);
  appendFranchiseStatistics(gyms, users);
  appendGyms(gyms);
  appendUsers(users);
  initSearch({ data: users, token, API: XANO_SUPER_ADMIN_USER_SEARCH_URL }, appendUsers);
}

function appendFranchiseStatistics(gyms, users) {
  if (gyms && gyms.length > 0) {
    // ? get users who were created within 24 days
    const newUsers = users.filter((u) => {
      const today = new Date();
      const createdDate = new Date(u.created_at);
      const diffTime = Math.abs(today - createdDate);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      return diffDays <= 24;
    });

    document.getElementById("gym-statistics-new-sign-ups").innerText = newUsers.length;
    document.getElementById("gym-statistics-attendence").innerText = gyms.length;
    document.getElementById("gym-statistics-top-performer").innerText = gyms[0].code; // ? Change this to be dependent on footfall
    document.getElementById("gym-statistics-total-members").innerText = users.length;
  }
}
function appendGyms(data) {
  const gymContainer = document.getElementById("gym-container");
  const og_webflow_element = gymContainer.querySelector("#gym-item");
  console.log(data);
  data.forEach((gym) => {
    const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
    clonedCard.setAttribute("id", "");
    clonedCard.setAttribute("href", `/admin/gym/g?gymCode=${gym.id}`);

    if (gym.map_icon) clonedCard.querySelector("#gym-map").style.backgroundImage = `url(${gym.map_icon.url})`;

    clonedCard.querySelector("#gym-code").innerText = gym.code;
    clonedCard.querySelector("#gym-name").innerText = gym.location;
    clonedCard.querySelector("#gym-coach").innerText = gym.partner || "NaN";
    // ? TODO: change this to footfall for the gym?
    clonedCard.querySelector("#gym-active-sessions").innerText = gym?.sessions?.length ?? 0;
    clonedCard.querySelector("#gym-revenue").innerText =
      Number(gym?.finance?.gross_revenue) + Number(gym?.finance?.additional_revenue) ?? "NaN";

    clonedCard.querySelector("#gym-active-users").innerText =
      gym?.users?.filter((user) => user.membership_status !== "INACTIVE" && user.membership_status !== "TRIAL")
        .length ?? 0;

    clonedCard.querySelector("#gym-users").innerText = gym?.users?.length ?? 0;

    gymContainer.prepend(clonedCard);
  });
  og_webflow_element.remove();
}

function appendUsers(data) {
  const tableContainer = document.getElementById("table-container");
  const og_webflow_element = tableContainer.querySelector("#table-item");

  function appendUsersFn(_data) {
    let allItems = tableContainer.querySelectorAll(".adb-user.w-inline-block");
    allItems.forEach((item, indx) => {
      if (indx !== allItems.length - 1) item.remove();
    });

    _data.forEach((user, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      clonedCard.setAttribute("href", `/admin/user?userId=${user.id}`);
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      if (user.profile_picture) el[0].style.backgroundImage = `url(${user.profile_picture.url})`;

      el[1].innerText = user.name;
      el[2].innerText = user.code;
      el[3].innerText = user.membership_status || "INACTIVE";
      el[4].innerText = user.gender;
      el[5].innerText = calculateAgeFromDOB(user.date_of_birth);
      el[6].innerText = "NaN";

      clonedCard.style.display = "flex";
      tableContainer.prepend(clonedCard);
    });
  }

  appendUsersFn(data);
  og_webflow_element.style.display = "none";
}

async function appendProfitChartData(finance) {
  const allMonths = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

  // get an array set of finance.month
  const months = [...new Set(finance.map((f) => f.month))].sort();
  const monthsRevenue = {};
  const monthsProfit = {};

  months.map((m) => {
    monthsRevenue[m] = finance
      .filter((f) => f.month === m)
      .reduce((acc, curr) => acc + curr.gross_revenue + curr.additional_revenue, 0);
    monthsProfit[m] = finance.filter((f) => f.month === m).reduce((acc, curr) => acc + curr.net_profit, 0);
  });

  const ctx = document.getElementById("dashboard-profits-chart").getContext("2d");

  ctx.canvas.parentNode.style.height = "400px";
  ctx.canvas.parentNode.style.width = "400px";

  // eslint-disable-next-line no-undef
   new Chart(ctx, {
    type: "bar",
    data: {
      labels: months.map((m) => allMonths[m - 1]),

      datasets: [
        {
          label: "Revenue",
          data: Object.values(monthsRevenue),
          backgroundColor: ["#00EDF4"],
        },
        {
          label: "Profit",
          data: Object.values(monthsProfit),
          backgroundColor: ["#E0FFC1"],
        },
      ],
    },
    options: {
      scales: {
        x: {
          grid: { display: false },
          stacked: true,
        },
        y: {
          grid: { display: false },
          stacked: true,
        },
      },
    },
  });
}
async function appendUsersChartData(users) {
  const activeUsers = users.filter((u) => u.membership_status !== "INACTIVE");
  const ctx = document.getElementById("dashboard-sessions-chart").getContext("2d");

  ctx.canvas.parentNode.style.height = "300px";
  ctx.canvas.parentNode.style.width = "300px";

  if (users.length === activeUsers.length) {
    // eslint-disable-next-line no-undef
     new Chart(ctx, {
      type: "pie",
      data: {
        labels: [`Total/Active Users ${users.length}`],

        datasets: [
          {
            label: "Revenue",
            data: [users.length],
            backgroundColor: ["#00EDF4"],
          },
        ],
      },
      options: {
        plugins: {
          legend: {
            position: "top",
          },
        },
      },
    });
  } else {
    // eslint-disable-next-line no-undef
     new Chart(ctx, {
      type: "pie",
      data: {
        labels: [`Total Users ${users.length}`, `Active Users ${activeUsers.length}`],

        datasets: [
          {
            label: "Revenue",
            data: [users.length, activeUsers.length],
            backgroundColor: ["#00EDF4", "#E0FFC1"],
          },
        ],
      },
      options: {
        plugins: {
          legend: {
            position: "top",
          },
        },
      },
    });
  }
}
